.Features {
  height: -moz-fit-content;
  height: fit-content;

  background-color: #f5f5f5; /* Light gray background color */
  background-image: linear-gradient(
      45deg,
      rgba(0, 0, 0, 0.05) 25%,
      transparent 25%,
      transparent 75%,
      rgba(0, 0, 0, 0.05) 75%,
      rgba(0, 0, 0, 0.05)
    ),
    linear-gradient(
      45deg,
      rgba(0, 0, 0, 0.05) 25%,
      transparent 25%,
      transparent 75%,
      rgba(0, 0, 0, 0.05) 75%,
      rgba(0, 0, 0, 0.05)
    );
  background-size: 20px 20px; /* Size of the texture pattern */
  background-blend-mode: overlay; /* Blend the gradients */
  background-size: 40px 40px;
  border-radius: 25px;
}
.boxEndorse {
  border-radius: 25px;
  background-color: white;
}

.sticky-container {
  position: relative;
  display: flex;
  align-items: center;
}

.sticky-element {
  position: absolute; /* Positioning the sticky element within the container */
  top: 0;
  width: 100%;
}
